.good{
    color: black;
    background: lightgreen;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    border-color: darkgreen;
    padding: 10px;
    margin-bottom: 10px;
    text-align: center;
}

.error{
    color: black;
    background: red;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    border-color: darkred;
    padding: 10px;
    margin-bottom: 10px;
    text-align: center;
}